.embed-container {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
  max-width: 100%; }

.embed-container iframe, .embed-container object embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }

.unity3-audio-transcription {
  border-style: solid;
  border-width: 1px 1px 1px 1px;
  border-color: #ebebeb; }
  .unity3-audio-transcription .tab {
    width: 100%;
    overflow: hidden; }
    .unity3-audio-transcription .tab-label {
      display: flex;
      justify-content: space-between;
      padding: 1em;
      margin: 0;
      background: #eee;
      font-weight: bold;
      cursor: pointer;
      /* Icon */ }
      .unity3-audio-transcription .tab-label::after {
        content: "\276F";
        width: 1em;
        height: 1em;
        text-align: center;
        transition: all .35s; }
    .unity3-audio-transcription .tab-content {
      height: 0;
      padding: 0 1em;
      transition: all .35s; }
    .unity3-audio-transcription .tab-close {
      display: flex;
      justify-content: flex-end;
      padding: 1em;
      font-size: 0.75em;
      background: #eee;
      cursor: pointer; }
      .unity3-audio-transcription .tab-close:hover {
        background: #d5d5d5; }
  .unity3-audio-transcription input.tab-toggle {
    position: absolute;
    opacity: 0;
    z-index: -1; }
  .unity3-audio-transcription input:checked + .tab-label {
    background: #d5d5d5; }
    .unity3-audio-transcription input:checked + .tab-label::after {
      transform: rotate(90deg); }
  .unity3-audio-transcription input:checked ~ .tab-content {
    height: auto;
    padding: 1em; }

.unity3-img-fx-1 {
  opacity: 1;
  transition: 0.5s; }
  .unity3-img-fx-1:hover {
    opacity: .6; }

